import blog from '@/api/blog'

export default {
  data () {
    return {
      blogs:[],
      user:{},
      page:1,
      total:0
    }
  },
  created(){
    this.userId = this.$route.params.userId
    this.page = this.$route.query.page || 1  //根据url默认去到指定页
    console.log('here')
    blog.getBlogsByUserId(this.userId,{ page:this.page })
      .then((res)=>{
        this.page  = res.page
        this.total = res.total
        this.blogs = res.data
        if(res.data.length > 0){
          this.user = res.data[0].user
          console.log(this.blogs)
        }
      })
  },
  methods:{
    splitDate(data){
      let dateObj = typeof data === 'object' ? data : new Date(data)
      return {
        date: dateObj.getDate(),
        month: dateObj.getMonth()+1,
        year: dateObj.getFullYear()
      }
    },
    onPageChange(a){
      blog.getIndexBlogs({ page: a}).then((res)=>{
        this.blogs = res
        this.total = res.total
        this.page = res.page
        this.$router.push({ path:`/user/${this.userId}`, query: {page: a}})
        console.log(this.page)
      })
    }
  }
}


